.feed-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
}

#feed {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.button-container {
  display: flex;
  justify-content: flex-start;
}

button {
  padding: 10px 20px;
  background-color: #78cc9c;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
  color: rgba(45, 130, 102, 0.847);
  font-weight: bold;
}

button:hover {
  background-color: #b8f4cc;
}

#donations-list {
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 5px;
  width: 300px;
  max-width: 90%;
}

#donations-list h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

